'use strict'

const imageTransform = require('./imageTransform')

module.exports = (staticMediaUrl, imageData, containerWidth, containerHeight, fittingType) =>
  imageTransform.getFromImageData(
    staticMediaUrl,
    imageData,
    containerWidth,
    containerHeight,
    fittingType || imageTransform.getImageScale(imageData, containerWidth, containerHeight),
    imageTransform.alignTypes.CENTER
  ).uri



