'use strict'

exports.joinUrl = (url, ...segments) =>
  segments.reduce((previous, current) => {
    return previous.replace(/\/$/, '') + '/' + current.replace(/^\//, '')
  }, url)

exports.isExternalUrl = url =>
  (/(^https?)|(^data)/).test(url)



