const {Children, Component, PropTypes} = require('react')

module.exports = class ServiceContainer extends Component {
  constructor(props) {
    super(props)
    this.services = props.services
  }

  getChildContext() {
    return {
      services: this.services(),
      addServices: this.services
    }
  }

  render() {
    return Children.only(this.props.children)
  }

}

module.exports.childContextTypes = {
  services: PropTypes.object,
  addServices: PropTypes.func
}



